const reducer = (state, action) => {
  let candidate
  let newState, job
  switch (action.type) {
    case 'QUICK_MATCH_CANDIDATE':
      // console.log(action)
      // console.log(state.recruiter.activeJobs[action.jobId])
      candidate = state.recruiter.activeJobs[action.jobId].new_matches[action.candidateId]
      // There's something wrong here, its double dispatching without the same state...
      if (!candidate) {
        return {
          ...state,
        }
      }
      // console.log(candidate)
      newState = Object.assign({}, state);
      delete newState.recruiter.activeJobs[action.jobId].new_matches[action.candidateId]
      if (getRandomInt(0, 3) === 3) {
        newState.recruiter.activeJobs[action.jobId].in_process[action.candidateId] = candidate
        newState.notifications.show_match_modal = true
        newState.notifications.candidate_id_match_modal = action.candidateId
      }
      // console.log(newState.recruiter.activeJobs[action.jobId])
      return {
        ...newState,
      };
    case 'CANCEL_PROCESS_FOR_CANDIDATE':
      // console.log(action)
      // console.log(state.recruiter.activeJobs[action.jobId])
      candidate = state.recruiter.activeJobs[action.jobId].in_process[action.candidateId]
      // There's something wrong here, its double dispatching without the same state...
      if (!candidate) {
        return {
          ...state,
        }
      }
      // console.log(candidate)
      newState = Object.assign({}, state);
      delete newState.recruiter.activeJobs[action.jobId].in_process[action.candidateId]
      // console.log(newState.recruiter.activeJobs[action.jobId])
      return {
        ...newState,
      };
    case 'REQUEST_CV':
      // console.log(action)
      // console.log(state.recruiter.activeJobs[action.jobId])
      candidate = state.recruiter.activeJobs[action.jobId].in_process[action.candidateId]
      // There's something wrong here, its double dispatching without the same state...
      if (!candidate) {
        return {
          ...state,
        }
      }
      // console.log(candidate)
      newState = Object.assign({}, state);
      newState.recruiter.activeJobs[action.jobId].in_process[action.candidateId].cv_requested = true
      // console.log(newState.recruiter.activeJobs[action.jobId])
      return {
        ...newState,
      };
    case 'HIDE_MATCH_MODAL':
      return {
        ...state,
        notifications: {
          ...state.notifications,
          show_match_modal: false,
        }
      };
    case 'HIDE_WELCOME_MODAL':
      return {
        ...state,
        notifications: {
          ...state.notifications,
          show_welcome: false,
        }
      };
    case 'ARCHIVE_JOB':
      job = state.recruiter.activeJobs[action.job_id]
      if (!job) {
        return {
          ...state,
        }
      }
      newState = Object.assign({}, state);
      delete newState.recruiter.activeJobs[action.job_id]
      newState.recruiter.archivedJobs[action.job_id] = job
      // console.log(newState.recruiter.activeJobs[action.jobId])
      return {
        ...newState,
      };
    case 'REPOST_JOB':
      job = state.recruiter.archivedJobs[action.job_id]
      if (!job) {
        return {
          ...state,
        }
      }
      newState = Object.assign({}, state);
      delete newState.recruiter.archivedJobs[action.job_id]
      newState.recruiter.activeJobs[action.job_id] = job
      return {
        ...newState,
      };
    case 'DELETE_JOB':
      job = state.recruiter.archivedJobs[action.job_id]
      if (!job) {
        return {
          ...state,
        }
      }
      newState = Object.assign({}, state);
      delete newState.recruiter.archivedJobs[action.job_id]
      return {
        ...newState,
      };
      case 'SEARCH':
        return {
          ...state,
          search: {
            ...state.search,
            what: action.what,
            where: action.where,
          }
        };
    default:
      return state
  }
}

